<template>
  <div class="newreport">
    <el-card>
      <div slot="header">
        <span class="card-name">报表设置</span>
      </div>
      <el-form label-width="100px">
        <el-form-item label="样本分析">
          <sample-select v-on:update:selected="getVars" :init-sample.sync="initSample"></sample-select>
        </el-form-item>
        <el-form-item label="报表名称">
          <el-input v-model.trim="reportname" placeholder></el-input>
        </el-form-item>
        <el-form-item label="高级筛选">
          <el-switch v-model="showFiltrate" active-color="#409eff"></el-switch>
        </el-form-item>
        <div v-show="showFiltrate">
          <el-form-item :label="`预筛选${index+1}`" v-for="(one,index) in prefilter" :key="index">
            <var-select
              v-on:update:selected="newV => {one.fieldName = newV[1]; one.initVar = newV}"
              :init-var.sync="one.initVar"
              :var-list.sync="varList"
              style="margin-right: 30px;"
            ></var-select>
            <el-select class="select-compare" v-model.trim="one.comparison" placeholder="请选择">
              <el-option
                v-for="(item, index) in operatorlist"
                :key="index"
                :label="item.text"
                :value="item.value"
              ></el-option>
            </el-select>
            <el-input v-model.trim="one.value" style="width: 220px; margin-right: 30px;"></el-input>
            <span class="btn-comm-add" @click.stop="addPrefilter" title="添加预筛选" v-if="index===0"></span>
            <span class="btn-comm-del" @click.stop="delPrefilter(index)" title="删除" v-if="index>0"></span>
          </el-form-item>
        </div>
        <el-form-item label="报表类型">
          <el-radio-group v-model.trim="reporttype">
            <el-radio
              v-for="item in reporttypelist"
              :key="item.tag"
              :label="item.value"
            >{{item.label}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <div v-for="(one, i) in varselectlist" :key="one.id" v-show="i==0 || showsecondvar">
          <el-form-item :label="`变量 ${i+1} 选择`">
            <var-select
              v-on:update:selected="newV => changeVar(one, newV)"
              :init-var.sync="one.initVar"
              :var-list.sync="varList"
            ></var-select>
            <el-popover
              class="el-popper__custom"
              placement="right"
              width="250"
              v-show="one.totalIV"
              trigger="hover"
            >
              <div>
                <div class="el-popover__title">
                  <img src="../assets/icon-brow.png" alt>蜂鸟智能
                </div>
                <p>
                  <b style="color: #409eff">自动IV值：</b>
                  <span>{{one.totalIV}}</span>
                </p>
              </div>
              <img src="../assets/bird-2.gif" alt slot="reference">
            </el-popover>
          </el-form-item>
          <el-form-item label="变量分组方式">
            <el-select v-model="one.statisticType" placeholder="请选择变量分组方式">
              <el-option
                v-for="(item, index) in one.sectioncontent"
                :key="index"
                :label="item.text"
                :value="item.value"
                :disabled="item.disabled"
              ></el-option>
            </el-select>
            <el-popover
              class="el-popper__custom"
              placement="right"
              width="400"
              v-show="one.smartRange"
              trigger="hover"
            >
              <div>
                <div class="el-popover__title">
                  <img src="../assets/icon-brow.png" alt>蜂鸟智能
                </div>
                <p>
                  <b style="color: #409eff">智能分组：</b>
                  <span>根据大数据智能分析确定分组</span>
                </p>
                <p
                  style="padding: 5px;border: 1px solid #ddd;"
                  v-show="one.smartRange"
                >{{one.smartRange}}</p>
              </div>
              <img src="../assets/bird-2.gif" alt slot="reference">
            </el-popover>
          </el-form-item>
          <el-form-item v-show="one.statisticType === 'RATIO_OF_EQUALITY'">
            <el-input class="mini-input" v-model.trim="one.minValue" placeholder="最小值"></el-input>
            <el-input class="mini-input" v-model.trim="one.maxValue" placeholder="最大值"></el-input>
            <el-input class="mini-input" v-model.trim="one.intervalValue" placeholder="间隔"></el-input>
          </el-form-item>
          <el-form-item v-show="one.statisticType === 'FIELD_RANGE'">
            <el-input
              class="comm-textarea"
              type="textarea"
              :rows="5"
              placeholder="请按照以下格式填写：每组最小值和最大值以~分隔，两组之间以英文逗号分隔，如100~300, 300~500"
              v-model.trim="one.rangeListStr"
            ></el-input>
          </el-form-item>
        </div>
        <el-form-item label="指标类型">
          <el-select v-model.trim="indicator" @change="selectIndicator" placeholder="请选择指标类型">
            <el-option
              v-for="(item, index) in indicatorlist"
              :key="index"
              :label="item.text"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="统计方式" v-show="showloanbefore">
          <el-radio-group v-model.trim="beforeway" style="margin: 0 30px 0 0;">
            <el-radio v-for="item in countway" :key="item.tag" :label="item.value">{{item.label}}</el-radio>
          </el-radio-group>
          <var-select
            v-on:update:selected="newV => {avgvar = newV[1]; initAvgvar = newV}"
            :init-var.sync="initAvgvar"
            :var-list.sync="varList"
          ></var-select>
          <el-checkbox v-model.trim="ispercent" style="margin-left: 30px;">返回百分比</el-checkbox>
        </el-form-item>
        <el-form-item label="逾期指标" v-show="showloanafter">
          <el-select class="select-loadtag" v-model.trim="overdue" filterable placeholder="请选择逾期指标">
            <el-option
              v-for="(option, index) in overduelist"
              :key="index"
              :label="option.text"
              :value="option.value"
            ></el-option>
          </el-select>
          <span class="var-label">类型</span>
          <el-select class="select-compare" v-model.trim="aftertype" filterable placeholder="请选择">
            <el-option
              v-for="(item, index) in aftertypelist"
              :key="index"
              :label="item.text"
              :value="item.value"
            ></el-option>
          </el-select>
          <span class="var-label">统计值</span>
          <el-radio-group v-model.trim="aftercount" style="margin: 0 30px 0 0;">
            <el-radio v-for="item in countvalue" :key="item.tag" :label="item.value">{{item.label}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <div v-show="hasmonitor">
          <el-form-item label="是否加入监控">
            <el-radio-group v-model.trim="ismonitor">
              <el-radio
                v-for="item in monitorlist"
                :key="item.tag"
                :label="item.value"
              >{{item.label}}</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="阀值设置">
            <el-input v-model.number.trim="thresholdmin" placeholder="最小值" style="width:100px;"></el-input>
            <el-input
              v-model.number.trim="thresholdmax"
              placeholder="最大值"
              style="width:100px;margin:0 20px;"
            ></el-input>
            <span class="var-label">浮动范围</span>
            <el-input v-model.number.trim="floatingrange" style="width:180px; margin-left:0;">
              <template slot="append">%</template>
            </el-input>
          </el-form-item>
        </div>
        <el-form-item label>
          <el-button type="primary" @click="createReport">{{reportid ? '保存报表' : '生成报表'}}</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import sampleSelect from '@/components/sample-select.vue';
import varSelect from '@/components/var-select.vue';
import {createReport, getReportDetail} from '@/api/monitor.js';
import {queryVarAutoSplit} from '@/api/analysis.js';
import {getVarList} from '@/api/sample.js';
import {
  getOverdueList,
  getSectionContent,
  getOperatorList,
  getCountway,
  getCountvalue,
  getIndicatorlist,
  getOneVarSelect,
  getAftertypelist,
  getOnePrefilter
} from '@/assets/scripts/getSomeSetting.js';

export default {
  name: 'page-newreport',
  props: ['moduleid'],
  components: {
    sampleSelect,
    varSelect
  },
  data() {
    return {
      initSample: [],
      //报表名
      reportname: '',
      //选择的样本名
      samplename: '',
      //所有样本数据
      allsamples: [],
      //报表类型可选的内容
      reporttypelist: [
        {label: '单变量+日期', tag: 'a10', value: 'SingleAndDate'},
        {label: '多变量', tag: 'a11', value: 'Multivariate'},
        {label: '多变量+日期', tag: 'a12', value: 'MultivariateAndDate'}
      ],
      //选择的报表类型,默认是单变量
      reporttype: 'SingleAndDate',
      showFiltrate: false,
      //预筛选默认的一条
      prefilter: [getOnePrefilter()],
      //预筛选的运算条件可选的内容
      operatorlist: getOperatorList(),
      //所有变量
      varList: [],
      //变量1选择
      varselectlist: [getOneVarSelect(), getOneVarSelect()],
      //变量分组方式可选的内容
      // sectioncontent: getSectionContent(),
      //指标类型可选的内容
      indicatorlist: getIndicatorlist(),
      //选择的指标类型
      indicator: '',
      showloanbefore: false,
      showloanafter: false,
      countway: getCountway(),
      countvalue: getCountvalue(),
      //是否返回百分比默认不选择
      ispercent: true,
      //用户选择的贷前的统计方式--默认是计数
      beforeway: 'count',
      //用户选择的贷前的统计方式为平均值时选择的变量
      avgvar: '',
      initAvgvar: [],
      //用户选择的贷后统计值,默认是逾期量
      aftercount: 'count',
      //贷后类型下的类型可选的内容
      aftertypelist: getAftertypelist(),
      //用户选择的贷后类型下的类型,默认是金额
      aftertype: 'peoples',
      //用户选择的贷后类型下的逾期指标
      overdue: '',
      //贷后类型下的逾期指标可选的内容
      overduelist: getOverdueList(),
      //是否加入监控可选的内容
      monitorlist: [
        {label: '是', tag: 'monitorlist10', value: true},
        {label: '否', tag: 'monitorlist11', value: false}
      ],
      //是否加入监控的选择,默认是是
      ismonitor: true,
      //阀值最小值
      thresholdmin: '',
      //阀值最大值
      thresholdmax: '',
      //浮动范围
      floatingrange: '',
      cansubmit: true
    };
  },
  computed: {
    showsecondvar() {
      return this.reporttype !== 'SingleAndDate';
    },
    showavgvar() {
      //是否显示用户选择的贷前的统计方式为平均值时的选择变量select,因为默认是计数所以默认不显示此select
      return this.beforeway === 'count' ? false : true;
    },
    showmontorset() {
      return this.ismonitor === true;
    },
    hasmonitor() {
      //是否有监控这项
      return this.reporttype !== 'Multivariate';
    }
  },
  watch: {
    $route: {
      handler: function(to, from) {
        if (
          from &&
          ((from.name === 'modulelist-report-setting' && to.name === 'modulelist-newreport') ||
            (from.name === 'modulelist-newreport' && to.name === 'modulelist-report-setting'))
        ) {
          this.$router.replace({
            path: '/empty',
            query: {
              callbackurl: to.path
            }
          });
        }
      },
      immediate: true
    }
  },
  methods: {
    getVars(val, isInit = false) {
      console.log('===========获取变量==========');
      this.rcProductName = val[0];
      this.moduleType = val[1];
      this.samplename = val[2];
      this.initSample = val;
      getVarList(this.samplename)
        .then(result => {
          if (Array.isArray(result.result)) {
            if (!result.result.length) {
              return;
            }
            this.varList = result.result.map(({key, vbname, fieldtype, transmittype}) => {
              return {
                label: `${vbname}${fieldtype === 'float' ? '（连续）' : '（离散）'}`,
                value: key,
                fieldtype,
                transmittype
              };
            });
            if (!isInit) {
              this.varselectlist.forEach((v, i) => {
                v.fieldName = '';
                v.statisticType = '';
                v.minValue = '';
                v.maxValue = '';
                v.intervalValue = '';
                v.rangeListStr = '';
                v.smartRange = '';
                v.totalIV = '';
                v.initVar = [];
              });
            } else {
              if (this.avgvar) {
                const theVar = this.varList.find(one => one.value === this.avgvar) || {};
                this.initAvgvar = [theVar.transmittype, theVar.value];
              }
              if (this.originData.filterConditionList && this.originData.filterConditionList.length) {
                this.showFiltrate = true;
                for (let i = 0; i < this.originData.filterConditionList.length; i++) {
                  const item = this.originData.filterConditionList[i];
                  // 处理数据，返回的filterConditionList上不存在initVar
                  const theVar = this.varList.find(one => one.value === item.fieldName) || {};
                  item.initVar = [theVar.transmittype, theVar.value];
                }
                this.prefilter = this.originData.filterConditionList;
              }
              for (let i = 0; i < this.originData.statisticConditionList.length; i++) {
                const item = this.originData.statisticConditionList[i];
                if (!(Array.isArray(item.sectioncontent) && item.sectioncontent.length)) {
                  item.sectioncontent = getSectionContent();
                }
                if (!Array.isArray(item.initVar)) {
                  const theVar = this.varList.find(one => one.value === item.fieldName) || {};
                  item.initVar = [theVar.transmittype, theVar.value];
                  this.changeVar(this.varselectlist[i], item.initVar);
                }
                if (Array.isArray(item.rangeList)) {
                  item.rangeListStr = item.rangeList.map(range => `${range.rangeMin}~${range.rangeMax}`).join(', ');
                }
                Object.assign(this.varselectlist[i], item);
              }
            }
          }
        })
        .catch(err => console.log(err));
    },
    changeVar(target = {}, val = []) {
      target.fieldName = val[1];
      target.initVar = val;
      const theVar = this.varList.find(item => item.value === val[1]) || {};
      console.log(theVar);
      if (theVar.fieldtype === 'float') {
        queryVarAutoSplit(this.samplename, target.fieldName)
          .then(result => {
            target.smartRange = '';
            target.totalIV = '';
            target.sectioncontent[0].disabled = true;
            if (Array.isArray(result.result.variableAreaRangeList)) {
              target.rangeList = result.result.variableAreaRangeList;
              let arr = target.rangeList.map(item => {
                return `${item.rangeMin}~${item.rangeMax}`;
              });
              target.smartRange = arr.join(', ');
              target.totalIV = result.result.totalIV.toFixed(2);
              if (target.smartRange.length > 0) {
                target.statisticType = 'SMART_RANGE';
                target.sectioncontent[0].disabled = false;
              }
            }
          })
          .catch(err => {
            if (target.statisticType === 'SMART_RANGE') {
              target.statisticType = '';
            }
            target.sectioncontent[0].disabled = true;
            target.smartRange = '';
            target.totalIV = '';
            console.log(err);
          });
      } else {
        if (target.statisticType === 'SMART_RANGE') {
          target.statisticType = '';
        }
        target.sectioncontent[0].disabled = true;
        target.smartRange = '';
        target.totalIV = '';
      }
    },
    addPrefilter() {
      this.prefilter.push(getOnePrefilter());
    },
    delPrefilter(index) {
      this.prefilter.splice(index, 1);
    },
    selectIndicator() {
      if (this.indicator === 'BEFORE_LOAN') {
        this.showloanbefore = true;
        this.showloanafter = false;
      } else if (this.indicator === 'AFTER_LOAN') {
        this.showloanbefore = false;
        this.showloanafter = true;
        this.beforeway = 'count';
        this.avgvar = '';
      }
    },
    checkForm() {
      console.log('=========开始检查表单=========');
      if (this.reportname.length === 0) {
        this.$message.warning('请填写报表名称');
        return;
      }
      if (this.samplename.length === 0) {
        this.$message.warning('请选择样本');
        return;
      }
      let length = this.showsecondvar ? 2 : 1;
      let hasError = false;
      for (let k = 0; k < length; k++) {
        let one = this.varselectlist[k];
        if (one.fieldName.length === 0) {
          this.$message.warning(`请选择变量${k + 1}中的变量`);
          hasError = true;
          break;
        }
        if (one.statisticType.length === 0) {
          this.$message.warning(`请选择变量${k + 1}中的变量分组方式`);
          hasError = true;
          break;
        }
        if (one.statisticType === 'RATIO_OF_EQUALITY') {
          if (one.minValue.length === 0 || one.maxValue.length === 0 || one.intervalValue.length === 0) {
            this.$message.warning(`请填写变量${k + 1}中等比分段的最小值、最大值、间隔`);
            hasError = true;
            break;
          }
        } else if (one.statisticType === 'FIELD_RANGE') {
          if (!one.rangeListStr) {
            this.$message.warning(`请填写变量${k + 1}自定义分组`);
            hasError = true;
            break;
          }
          let str = one.rangeListStr;
          let arr = str.split(',');
          one.rangeList = [];
          for (let i = 0; i < arr.length; i++) {
            let rangeArr = arr[i].split('~');
            one.rangeList.push({
              rangeMin: rangeArr[0] && rangeArr[0].trim(),
              rangeMax: rangeArr[1] && rangeArr[1].trim()
            });
          }
          for (let i = 0; i < one.rangeList.length; i++) {
            let range = one.rangeList[i] || {};
            let reg = /^(-?\d+)(\.\d+)?$/;
            let rangeMin = range.rangeMin;
            let rangeMax = range.rangeMax;
            if ((rangeMin !== 'null' && !reg.test(rangeMin)) || (rangeMax !== 'null' && !reg.test(rangeMax))) {
              hasError = true;
              this.$message.warning(`变量${k + 1}自定义分组格式错误`);
              break;
            }
          }
        }
      }
      if (hasError) {
        return;
      }
      if (this.indicator.length === 0) {
        this.$message.warning('请选择指标类型');
        return;
      }
      if (this.indicator === 'BEFORE_LOAN' && this.beforeway === 'avg' && this.avgvar.length === 0) {
        this.$message.warning('请选择平均值对应的变量');
        return;
      }
      if (this.indicator === 'AFTER_LOAN' && this.overdue.length === 0) {
        this.$message.warning('请选择逾期指标');
        return;
      }
      if (this.hasmonitor) {
        if (this.ismonitor === true) {
          if (this.thresholdmin.length === 0) {
            this.$message.warning('请填写阀值的最小值');
            return;
          }
          if (this.thresholdmax.length === 0) {
            this.$message.warning('请填写阀值的最大值');
            return;
          }
          if (this.floatingrange.length === 0) {
            this.$message.warning('请填写浮动范围');
            return;
          }
        }
      }
      return true;
    },
    createReport() {
      console.log('=========开始生成报表=========');
      const that = this;
      if (!this.cansubmit) {
        console.log('======生成按钮不可点=====');
        return;
      }
      this.cansubmit = false;
      if (!this.checkForm()) {
        console.log('检查表单的结果不通过');
        this.cansubmit = true;
        return;
      }
      console.log('=======开始向后台发起请求======');
      let postdata = {};
      postdata.id = this.reportid;
      postdata.name = this.reportname;
      postdata.indexName = [this.samplename];
      postdata.productid = this.moduleid;
      postdata.reportType = this.reporttype;
      postdata.rcProductName = this.rcProductName;
      postdata.moduleType = Number(this.moduleType);

      let prefilter = this.prefilter.filter(function(v) {
        return v.comparison.length > 0 && v.fieldName.length > 0 && v.value.length > 0;
      });
      postdata.filterConditionList = this.showFiltrate ? prefilter : [];

      if (!this.showsecondvar) {
        postdata.statisticConditionList = this.varselectlist.slice(0, 1);
      } else {
        postdata.statisticConditionList = this.varselectlist;
      }
      postdata.loanType = this.indicator;

      if (this.indicator === 'BEFORE_LOAN') {
        //选择贷前
        if (this.beforeway === 'count') {
          //选择计数
          postdata.resultValueType = 'COUNT';
          postdata.resultField = '';
        }
        if (this.beforeway === 'avg') {
          //选择平均值
          postdata.resultValueType = 'FIELD_AVG';
          postdata.resultField = this.avgvar;
          postdata.initAvgvar = this.initAvgvar;
        }
        postdata.resultNeedPercent = this.ispercent;
      }
      if (this.indicator === 'AFTER_LOAN') {
        //选择贷后
        if (this.aftertype === 'peoples') {
          //类型选择人数
          postdata.resultValueType = 'COUNT';
          postdata.resultField = '';
        }
        if (this.aftertype === 'amount') {
          //类型选择金额
          postdata.resultValueType = 'FIELD_SUM';
          postdata.resultField = 'LoanCapital';
        }
        if (this.aftercount === 'count') {
          //统计值选择逾期量
          postdata.resultNeedPercent = false;
        }
        if (this.aftercount === 'rate') {
          //统计值选择逾期率
          postdata.resultNeedPercent = true;
        }
        let overduedata = this.overdue.split('-');
        postdata.afterLoanKPI = overduedata[0];
        postdata.overdueDays = overduedata[1];
      }

      if (this.hasmonitor) {
        //支持监控功能
        postdata.monitor = this.ismonitor;
        if (this.ismonitor) {
          //选择加入监控
          postdata.threshold = {
            min: this.thresholdmin,
            max: this.thresholdmax
          };
          postdata.floatingRange = this.floatingrange;
        }
      } else {
        //不支持监控功能
        postdata.monitor = false;
      }

      console.log(postdata);
      createReport(postdata)
        .then(result => {
          that.cansubmit = true;
          that.$message.success(`${this.reportid ? '报表更新成功' : '报表创建成功'}`);
          that.$router.go(-1);
          setTimeout(function() {
            that.cansubmit = true;
          }, 1000);
        })
        .catch(err => {
          console.log(err);
          that.cansubmit = true;
        });
    },
    getReportSetting() {
      getReportDetail(this.reportid)
        .then(result => {
          if (!result.result) {
            this.$message.warning('此报表不存在');
            return;
          }
          // 设置默认值
          const data = result.result;
          this.originData = data;
          console.log('=====开始设置默认值======');
          this.rcProductName = data.rcProductName;
          this.moduleType = String(data.moduleType);
          this.samplename = Array.isArray(data.indexName) ? data.indexName[0] : data.indexName;
          this.initSample = [this.rcProductName, this.moduleType, this.samplename];
          this.getVars(this.initSample, true);

          this.reportname = data.name;
          this.reporttype = data.reportType;

          if (data.loanType === 'BEFORE_LOAN') {
            //贷前
            this.indicator = 'BEFORE_LOAN';
            this.showloanbefore = true;
            this.showloanafter = false;
            if (data.resultValueType === 'COUNT') {
              this.beforeway = 'count';
            }
            if (data.resultValueType === 'FIELD_AVG') {
              this.beforeway = 'avg';
              this.avgvar = data.resultField;
            }
            this.ispercent = data.resultNeedPercent;
          }
          if (data.loanType === 'AFTER_LOAN') {
            //贷后
            this.indicator = 'AFTER_LOAN';
            this.showloanafter = true;
            this.showloanbefore = false;
            this.overdue = data.afterLoanKPI + '-' + data.overdueDays;
            if (data.resultValueType === 'COUNT') {
              this.aftertype = 'peoples';
            }
            if (data.resultValueType === 'FIELD_SUM') {
              this.aftertype = 'amount';
            }
            if (data.resultNeedPercent === true) {
              this.aftercount = 'rate';
            } else {
              this.aftercount = 'count';
            }
          }
          if (!data.monitor) {
            this.ismonitor = false;
            this.thresholdmin = '';
            this.thresholdmax = '';
            this.floatingrange = '';
          }
          if (data.monitor) {
            this.ismonitor = true;
            this.thresholdmin = data.threshold.min;
            this.thresholdmax = data.threshold.max;
            this.floatingrange = data.floatingRange;
          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  created() {
    this.moduleid = this.$route.params.moduleid;
    this.reportid = this.$route.params.reportid;

    if (this.moduleid && this.reportid) {
      //获取保存的分析结果
      this.getReportSetting();
    }
  },
  mounted() {}
};
</script>

